<template>
  <NavLogin
    ref="NavLogin"
    :ssg="confirm"
    @Logmemberin="logme"
    @reg="check"
  ></NavLogin>
  <Record ref="Record"></Record>
  <discount ref="Discount"></discount>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="mod1"
  >
    <div class="modal-dialog modal-xl modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">購物車</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="middle">
            <div class="modtitl"><h5>商品</h5></div>
            <div
              class="modtext"
              v-for="(spg, spgle) in newuser.shoppingcart"
              :key="spg.id"
            >
              <div class="modleft">
                <div
                  class="img"
                  :style="{ backgroundImage: 'url(' + spg.imageUrl + ')' }"
                ></div>
                <div class="modleftext">
                  <p>{{ spg.title }}</p>
                </div>
              </div>
              <div class="modrigth">
                <div class="lele">
                  <button @click="shoppingDelete(spgle)">刪除</button>
                </div>
                <div class="riri">
                  <div class="quantity">
                    <p>數量</p>
                    <input
                      type="number"
                      v-model="spg.people"
                      @input="alltotal"
                    />
                  </div>
                  <div class="price">
                    <h5>{{ spg.price }}</h5>
                    <p>元</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <div class="total">
            <div class="totaleft">
              <p>總金額</p>
            </div>
            <div class="totarigth">
              <h5>元</h5>
              <p>{{ Shoppingtoto }}</p>
            </div>
          </div>
          <div class="totalbottom">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              關閉
            </button>
            <button type="button" class="btn btn-primary" @click="shoppingCart">
              付款
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <nav class="navbar navbar-expand-lg">
    <div class="container-fluid">
      <div class="go" :class="{ gogo: anim }">
        <div class="Transi">
          <div class="bac">
            <div class="bacc"><div class="baccc"></div></div>
          </div>
        </div>
      </div>
      <div class="nas">
        <a
          class="navbar-brand"
          href="#"
          @click="
            blu = 1;
            remov();
          "
          ><img src="../imgoricons/icon/logo.svg" alt=""
        /></a>
      </div>
      <button
        class="navbar-toggler"
        type="button"
        @click="
          endnav = false;
          opennav = true;
        "
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavDropdown">
        <ul class="navbar-nav">
          <li
            class="nav-item"
            :class="{ n1: blu == 1 }"
            @click="
              blu = 1;
              remov();
            "
          >
            <a class="nav-link active" aria-current="page" href="#">首頁</a>
          </li>
          <li
            class="nav-item n2"
            :class="{ n1: blu == 2 }"
            @click="
              blu = 2;
              remov();
            "
          >
            <router-link class="nav-link" to="/seafishing"
              >海釣行程</router-link
            >
          </li>
          <li
            class="nav-item n3"
            :class="{ n1: blu == 3 }"
            @click="
              blu = 3;
              remov();
            "
          >
            <router-link class="nav-link" to="/CommoDity">海釣商品</router-link>
          </li>
          <li
            class="nav-item n4"
            :class="{ n1: blu == 4 }"
            @click="
              blu = 4;
              remov();
            "
          >
            <a class="nav-link" href="#">常見問題</a>
          </li>
        </ul>
      </div>
    </div>
